export default {
	props:{
		items:{
			type: Array
		},
		initValue: {
			type: String,
			default: ''
		},
		wid: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			index: this.initValue
		};
	},
	methods:{
		returnIndex:function(id){
			let item = this.items
			for (var i=0;i<item.length;i++){
				if (item[i].id == id){
					return i
				}
			}
		},
		buttonChange:function(id){
			this.index = id
			this.$emit("buttonChange",id)
		}
	}
}
